export default "@import (reference) \"nui-framework-variables\";\n@import (reference) \"mixins\";\n\n@box-width: 150px;\n@box-height: 100px;\n\n.draggable-item {\n    width: @box-width;\n    height: @box-height;\n    padding: @nui-space-md;\n    box-sizing: border-box;\n    border: solid 1px @gray;\n    display: inline-flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    position: relative;\n    z-index: 1;\n    .transition(box-shadow 0.4s);\n\n    &:hover, &.cdk-drag-dragging {\n        .nui-dnd-hover-state();\n        .nui-dnd-raised-state();\n        cursor: move;\n        padding: @nui-space-md;\n    }\n}\n";